      max-width: 500px;
      margin-top: 20px;
      line-height: 1.4;
      @media only screen and (max-width: 360px) {
        margin-top: 4px;
        font-size: 16px;
        line-height: 1.5;
      }
    }
    .icon-success {
      display: block;
