// popup 
.modal {
  padding-right: 0!important;
}

.modal-dialog {
  max-width: 800px!important;
  position: relative;
  @media only screen and (max-width: 840px) {
    max-width: calc(100vw - 40px)!important;
  }
  @media only screen and (max-width: 400px) {
    max-width: calc(100vw - 20px)!important;
  }
  button.close {
    position: absolute;
    right: 45px;
    top: 40px;
    width: auto;
    margin: 0 0 0 auto;
    display: inline-block;
    float: none;
    font-size: 1rem;
    z-index: 1;
    @media only screen and (max-width: 840px) {
      right: 5.6vw;
      top: 5vw;
    }
    @media only screen and (max-width: 360px) {
      right: 14px;
      top: 14px;
      img {
        width: 16px;
      }
    }
  }
}

.order-form--popup {
  margin: 0!important;
  padding-bottom: 25px;
  .form__header {
    max-width: 370px;
    margin: 0 auto;
    position: relative;
    .form__title {
      margin: 20px 0 0 0;
      font-size: 30px;
      font-weight: bold;
      letter-spacing: 1px;
      text-align: center;
    }
    .order-form__dots {
      top: -43px;
      left: -40px;
      z-index: 0;
      opacity: 0.6;
    }
    @media only screen and (max-width: 480px) {
      margin-bottom: 40px;
      .order-form__dots {
        top: auto;
        left: 33px;
        bottom: -40px;
        width: 75px;
      }
      .form__title {
        font-size: 20px;
      }
      .form__descr {
        font-size: 13px;
        line-height: 1.31;
      }
    }
  }
  .form__column {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .form-group {
      flex-basis: 47%;
    }
    @media only screen and (max-width: 580px) {
      display: block;
    }
  }
  label {
    color: #797e85;
  }
  @media only screen and (max-width: 480px) {
    padding-bottom: 20px;
    .checkbox [type=checkbox]:checked+label,
    .checkbox [type=checkbox]:not(:checked)+label {
      margin-right: 10px;
      padding-bottom: 4px;
    }
    .copy-privacy p {
      margin-bottom: 0;
    }
  }
  .copy-privacy {
    align-items: flex-start;
  }
  .form__submit {
    margin-top: 10px;
    width: 100%;
  }
}

.mr-30px {
  margin-right: 30px;
}

